:root{
--background:#f44236;
--text:white;
--title:white;
--button:white;
--element:white;
}
.progressBarContainer {
  width: 30%;
}
/* text inside clock */
h3 {
  /* font-size: 4vw; */
  margin: 0;
  color: var(--title);
  font-weight: 400;
}


.buttonsContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}

button {
  background-color: transparent;
  color: var(--button);
  border: none;
}

.settingsContainer {
  background-color: var(--background);
  display: flex;
  flex-direction: column;
  align-items: center;
}

label {
  color: var(--text);
 
}

.slider {
  border: 2px solid var(--element);
  border-radius: 20px;
}


.thumb {
  background-color: white;
  cursor: pointer;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  margin: -8px;
}

/* extra small */
@media screen and (max-width: 300px) {
  header {
    width: 90%;
    margin-bottom: 2rem;
    margin-top: 1.5rem;
  }

  h1 {
    font-size: 10vw;
  }
  h2 {
    font-size: 7vw;
    margin-top: 1rem;
  }

  h3 {
    font-size: 12vw;
  }

  .progressBarContainer {
    width: 80%;
  }

  .clockIcon {
    width: 12vw;
  }

  .buttonsContainer {
    width: 30%;
    margin-top: 1.5rem;
  }

  button {
    width: 100%;
  }

  /* ---------- settings ----------*/
  .settingsContainer {
    margin-top: 5rem;
  }

  label {
    font-size: 9.2vw;
    width: 70%;

}

  .slider {
    margin-top: 1rem;
    margin-bottom: 3rem;
    width: 70%;
  }
  .sliderBreak {
    margin-bottom: 2rem;
  }

  .confirmButton {
    width: 100%;
  }

  .confirmButtonDiv {
    width: 29%;
  }
}
