* {
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--background);
  font-family: "Roboto", sans-serif;
}

h1 {
  font-weight: bold;
  color: var(--title);
  border-bottom: 3px solid white;
}

 h2 {
  color: var(--text);
  font-weight: lighter;
}

.timerContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

